
<template>
  <div class="form-horizontal">
        <input
          type="number"
          v-model="totalTime"
          placeholder="Hours"
        />
        <button @click='save()'>保存</button>
    <hr>
  </div>
</template>

<script>
  export default {
        name : 'LogTime',
        data() {
            return {
                totalTime : ''
            }
        },
        methods: {
          save() {
            const plan = {
              totalTime : this.totalTime  
            }
            this.$store.dispatch('savePlan', plan)
            this.$store.dispatch('addTotalTime', this.totalTime)
            console.log('键入'+typeof(this.totalTime)+ this.totalTime)
            this.$router.go(-1)
          }
        }
    }
</script>



